import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";

export default function SignUp() {

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role, setrole] = useState("");

  const history = useHistory();
  const changeName = (e) => {
    setName(e.target.value);
  };
  const changeEmail = (e) => {
    setEmail(e.target.value);
  };
  const changePassword = (e) => {
    setPassword(e.target.value);
  };

  const addUser = async () => {
      console.log({
        name: name,
        email: email,
        password: password,
        role
      });
    const response = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/signUp`, {
      name: name,
      email: email,
      password: password,
      role
    });
        history.push("/login")
 
  };
  return ( <div className="addC">  
          <h2>   </h2>
          <hr/>
          <label>:  </label>
        <input className="inputC" onChange={(e) => {  changeName(e); }} type="text" placeholder="  " />
      <label>:   </label>
        <input className="inputC" onChange={(e) => { changeEmail(e); }} type="text" id="email" placeholder="  " />
        <label>:   </label>
        <input className="inputC"  onChange={(e) => { (changePassword(e)); }} type="password"  placeholder="  "/>
        <label>:   -  </label> <br/>
        <select onChange={(e) => { setrole(e.target.value);  }} >
                    <option ></option>
                    <option value="3"></option>
                    <option value="2"></option>
                  </select>
      <button className="buttt" onClick={(event) => {  addUser(event);  }}type="submit" >  </button> 
      </div>
        );
}


